	data() {
		return {
			type: '',
			items: [],
			// buylist: [], //订单列表
			goodsPrice: 0.0, //商品合计价格
			sumPrice: 0.0, //用户付款价格
			freight: 12.0, //运费
			note: '', //备注
			int: 1200, //抵扣积分
			deduction: 0 //抵扣价格
		};
	},
	onShow() {
		uni.request({
			url: this.$tempUrl + 'userAddress/list',
			method: 'GET',
			data: { openId: uni.getStorageSync('openId') },
			success: res => {
				console.log(res.data.data);
				this.items = res.data.data;
				this.items.forEach(item => {
					switch (item.label) {
						case '0':
							item.updateBy = '家';
							break;
						case '1':
							item.updateBy = '公司';
							break;
						case '2':
							item.updateBy = '学校';
							break;
						default:
							item.updateBy = item.contactName.substring(0, 1);
							break;
					}
				});
			}
		});
	},
	methods: {
		confirmAddr(item) {
			console.log('confirmAddr');
			uni.setStorage({
				key: 'confirmAddr',
				data: item,
				success: () => {
					uni.navigateBack({});
				}
			});
		},
		addButton() {
			console.log('addButton');
			uni.navigateTo({
				url: 'addressAdd'
			});
		},
		editButton(row) {
			console.log('editButton');
			uni.navigateTo({
				url:
					'addressUpd?id=' +
					row.id +
					'&contactName=' +
					row.contactName +
					'&contactPhone=' +
					row.contactPhone +
					'&areaArray=' +
					row.areaArray +
					'&address=' +
					row.address +
					'&label=' +
					row.label
			});
		}
	}
};
